@charset "UTF-8";
/*Глобальные стили*/
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Sans";
  font-display: swap;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-style: bold;
  font-weight: 700;
  font-family: "PT Sans";
  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
.container {
  width: 100%;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 18px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 60px;
  }
}
@media (min-width: 1200px) {
  .container {
    padding: 0 120px;
  }
}

.container-photo {
  width: 100%;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container-photo {
    padding: 0 60px;
  }
}

html {
  height: 100%;
}

body {
  display: flex;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  font-family: "PT Sans", "Arial", sans-serif;
  color: #000000;
  background-color: #f2f2f2;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a {
  text-decoration: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

/*Блоки страниц*/
.title {
  margin: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .title {
    font-size: 21px;
  }
}
.title--big {
  font-size: 18px;
}
@media (min-width: 768px) {
  .title--big {
    font-size: 30px;
  }
}

.main-text {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  color: #333333;
  text-transform: uppercase;
}

.button-link {
  display: block;
  width: 100%;
  padding: 16px;
  font-family: inherit;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  background-color: #81b3d2;
  border: none;
  cursor: pointer;
}
.button-link:hover {
  background-color: #669ec0;
}
.button-link:active, .button-link:focus {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}
.button-link:disabled {
  background-color: #e5e5e5;
  pointer-events: none;
}

.site-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .site-list {
    display: grid;
    background-color: transparent;
    grid-template-columns: repeat(5, 1fr);
  }
}
.site-list::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 7px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .site-list::after {
    display: none;
  }
}

.site-list__item {
  position: relative;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  border-bottom: 1px solid rgba(1, 1, 1, 0.2);
}
@media (min-width: 768px) {
  .site-list__item {
    padding: 7px 10px 2px 10px;
    border: none;
  }
}
.site-list__item:nth-child(3) {
  grid-column-start: 4;
}
.site-list__item:nth-child(7) {
  grid-column-start: 4;
}
.site-list__item:last-child {
  border-bottom: none;
}

.site-list__link {
  display: inline-flex;
  justify-content: center;
  width: 100%;
  padding: 17px 50px 11px 50px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #ffffff;
}
@media (min-width: 768px) {
  .site-list__link {
    width: auto;
    padding: 10px;
    font-size: 14px;
    color: #000000;
  }
}
.site-list__link:hover {
  background-color: #669ec0;
}
@media (min-width: 768px) {
  .site-list__link:hover {
    color: #81b3d2;
    background-color: transparent;
  }
}
.site-list__link:active, .site-list__link:focus {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}
@media (min-width: 768px) {
  .site-list__link:active, .site-list__link:focus {
    color: rgba(0, 0, 0, 0.3);
    background-color: transparent;
  }
}

.site-list__item--current .site-list__link {
  color: rgba(255, 255, 255, 0.3);
}
.site-list__item--current .site-list__link:hover, .site-list__item--current .site-list__link:active, .site-list__item--current .site-list__link:focus {
  color: #ffffff;
  background: transparent;
}
@media (min-width: 768px) {
  .site-list__item--current .site-list__link {
    color: #766357;
    pointer-events: none;
  }
}

.advantages {
  text-align: center;
  text-transform: uppercase;
}

.advantages__header {
  max-width: 380px;
  margin: 0 auto;
  padding: 34px 0 32px 0;
}
@media (min-width: 768px) {
  .advantages__header {
    max-width: 730px;
    padding: 49px 0 56px 0;
  }
}
@media (min-width: 1200px) {
  .advantages__header {
    padding: 59px 0 46px 0;
  }
}

.advantages__main-title {
  margin-bottom: 36px;
}
@media (min-width: 768px) {
  .advantages__main-title {
    margin-bottom: 28px;
  }
}

@media (max-width: 767px) {
  .advantages__main-title br,
.advantages__item-title br {
    display: none;
  }
}

.advantages__intro {
  margin: 0;
  color: #333333;
}

.advantages__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .advantages__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
