    padding: 24px 33px 40px 33px;
  }
}
@media (min-width: 1200px) {
  .advantages__item {
    padding: 52px 50px 40px 50px;
  }
}

.advantages__list-top .advantages__item {
  position: relative;
  margin-bottom: 24px;
  padding: 9px 0 0 46px;
}
.advantages__list-top .advantages__item:last-child {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .advantages__list-top .advantages__item {
    margin-bottom: 0;
    padding: 156px 33px 62px 33px;
  }
}
@media (min-width: 1200px) {
  .advantages__list-top .advantages__item {
    padding: 169px 50px 70px 50px;
  }
}
.advantages__list-top .advantages__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 40px;
  height: 40px;
  background-repeat: no-repeat;
  background-size: contain;
}
@media (min-width: 768px) {
  .advantages__list-top .advantages__item::before {
    left: 50%;
    transform: translateX(-50%);
  }
}
.advantages__list-top .advantages__item--hotel::before {
  background-image: url("../images/svg/home.svg");
}
@media (min-width: 768px) {
  .advantages__list-top .advantages__item--hotel::before {
    top: 60px;
    width: 77px;
    height: 72px;
  }
}
@media (min-width: 1200px) {
  .advantages__list-top .advantages__item--hotel::before {
    top: 69px;
    width: 79px;
    height: 72px;
  }
}
.advantages__list-top .advantages__item--food::before {
  background-image: url("../images/svg/burger.svg");
}
@media (min-width: 768px) {
  .advantages__list-top .advantages__item--food::before {
    top: 62px;
    width: 80px;
    height: 68px;
  }
}
@media (min-width: 1200px) {
  .advantages__list-top .advantages__item--food::before {
    top: 72px;
    width: 73px;
    height: 69px;
  }
}
.advantages__list-top .advantages__item--souvenir::before {
  background-image: url("../images/svg/gift-1.svg");
}
@media (min-width: 768px) {
  .advantages__list-top .advantages__item--souvenir::before {
    top: 64px;
    width: 67px;
    height: 75px;
  }
}
@media (min-width: 1200px) {
  .advantages__list-top .advantages__item--souvenir::before {
    top: 69px;
    width: 77px;
    height: 75px;
    background-image: url("../images/svg/gift-2.svg");
  }
}

.advantages__title {
  margin-bottom: 16px;
}
@media (max-width: 767px) {
  .advantages__title {
    text-align: left;
  }
}
@media (min-width: 768px) {
  .advantages__title {
    margin-bottom: 23px;
  }
}

.advantages__item-title {
  margin-bottom: 12px;
}
@media (min-width: 768px) {
  .advantages__item-title {
    margin-bottom: 15px;
  }
}

.advantages__number {
  display: block;
  margin-bottom: 17px;
  color: #333333;
}
@media (min-width: 768px) {
  .advantages__number {
    margin-bottom: 23px;
  }
}

.advantages__subject {
  margin: 0;
  color: #333333;
}
@media (max-width: 767px) {
  .advantages__subject {
    text-align: left;
  }
}

@media (max-width: 767px) {
  .advantages__subject br:not([class]) {
    display: none;
  }
}

@media (min-width: 768px) {
  .advantages__subject .advantages__br {
    display: none;
  }
}

.advantages__list-bottom {
  padding-bottom: 4px;
  background-color: #eeeeee;
}
@media (min-width: 768px) {
  .advantages__list-bottom {
    padding-bottom: 1px;
  }
}

.advantages__list-bottom .advantages__item {
  padding: 24px 26px;
}
@media (min-width: 768px) {
  .advantages__list-bottom .advantages__item {
    margin: auto 0;
    padding: 42px 33px 38px 33px;
  }
}
@media (min-width: 1200px) {
  .advantages__list-bottom .advantages__item {
    padding: 63px 50px 39px 50px;
  }
}
@media (min-width: 1200px) {
  .advantages__list-bottom .advantages__item:nth-child(2n) {
    transform: translateY(-11px);
  }
}

.advantages__list-bottom .advantages__subject {
  margin: 0 auto;
  text-align: center;
}
@media (min-width: 768px) {
  .advantages__list-bottom .advantages__subject {
    max-width: 180px;
  }
}
@media (min-width: 1200px) {
  .advantages__list-bottom .advantages__subject {
    max-width: 300px;
  }
}

@media (min-width: 768px) {
  .advantages__item--text .advantages__subject {
    max-width: 190px;
  }
}
@media (min-width: 1200px) {
  .advantages__item--text .advantages__subject {
    max-width: 300px;
  }
}

.advantages__list-bottom .advantages__item:not(:last-child) {
  border-bottom: 1px solid #d6d6d6;
}
@media (min-width: 768px) {
  .advantages__list-bottom .advantages__item:not(:last-child) {
    border: none;
  }
