.feedback {
  padding-top: 37px;
  padding-bottom: 27px;
}
@media (min-width: 768px) {
  .feedback {
    padding-top: 64px;
    padding-bottom: 74px;
  }
}
@media (min-width: 1200px) {
  .feedback {
    padding: 113px 120px 74px 120px;
  }
}

.feedback__header {
  max-width: 535px;
  margin: 0 auto;
}

.feedback__main-title {
  margin-bottom: 23px;
}
@media (min-width: 768px) {
  .feedback__main-title {
    margin-bottom: 22px;
  }
}
@media (min-width: 1200px) {
  .feedback__main-title {
    margin-bottom: 30px;
  }
}

.feedback__intro {
  margin: 0;
  margin-bottom: 29px;
  color: #333333;
}
@media (min-width: 768px) {
  .feedback__intro {
    margin-bottom: 76px;
    line-height: 24px;
  }
}
@media (min-width: 1200px) {
  .feedback__intro {
    margin-bottom: 42px;
  }
}

@media (max-width: 767px) {
  .feedback__intro br {
    display: none;
  }
}

@media (min-width: 768px) {
  .feedback__form-container {
    position: relative;
    display: grid;
    margin-bottom: 48px;
    grid-template-columns: repeat(2, 1fr);
  }
  .feedback__form-container::before {
    content: "";
    position: absolute;
    top: 24px;
    left: 50%;
    width: 1px;
    height: calc(100% - 25px);
    background-color: #dbdbdb;
    transform: translateX(-50%);
  }
}
@media (min-width: 768px) and (min-width: 1200px) {
  .feedback__form-container::before {
    left: calc(50% + 13px);
  }
}
@media (min-width: 1200px) {
  .feedback__form-container {
    margin-bottom: 56px;
    grid-template-columns: repeat(2, 1fr);
  }
}

.feedback__group {
  display: block;
  padding: 0;
  padding-bottom: 39px;
  border: none;
}
@media (min-width: 768px) {
  .feedback__group {
    padding-bottom: 16px;
  }
}

.feedback__actions {
  padding: 11px 2px 0 2px;
}
@media (min-width: 768px) {
  .feedback__actions {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1200px) {
  .feedback__actions {
    padding: 0 96px;
  }
}

.feedback__actions-description {
  margin: 0;
  font-size: 12px;
  text-align: right;
  color: #333333;
  text-transform: uppercase;
  opacity: 0.4;
}
@media (min-width: 768px) {
  .feedback__actions-description {
    margin: auto 0;
    font-size: 14px;
    transform: translateY(5px);
    opacity: 1;
  }
}

