.main-card {
  background-color: #81b3d2;
  clip-path: polygon(50% 4%, 100% 1%, 100% 100%, 0 100%, 0 1%);
}
@media (min-width: 768px) {
  .main-card {
    display: grid;
    min-height: 256px;
    clip-path: none;
    grid-template-columns: repeat(3, 1fr);
  }
}
.main-card--second {
  clip-path: none;
}

.main-card__inner {
  margin: auto;
  padding: 54px 17px 39px 17px;
  color: #ffffff;
}
@media (min-width: 768px) {
  .main-card__inner {
    padding-bottom: 31px;
  }
}
@media (min-width: 1200px) {
  .main-card__inner {
    padding: 33px 17px 31px 17px;
  }
}

.main-card--second .main-card__inner {
  padding: 38px 17px 36px 17px;
  grid-column: 3/-1;
}
@media (min-width: 768px) {
  .main-card--second .main-card__inner {
    padding: 53px 17px 31px 17px;
  }
}
@media (min-width: 1200px) {
  .main-card--second .main-card__inner {
    padding-top: 34px;
  }
}

.main-card__title {
  max-width: 216px;
  margin: 0 auto;
  margin-bottom: 11px;
  color: #ffffff;
}

.main-card__number {
  display: block;
  margin-bottom: 17px;
  color: #ffffff;
}
@media (min-width: 768px) {
  .main-card__number {
    margin-bottom: 24px;
  }
}

.main-card__subject {
  max-width: 284px;
  margin: 0 auto;
  color: #ffffff;
}
@media (min-width: 768px) {
  .main-card__subject {
    max-width: 197px;
  }
}
@media (min-width: 1200px) {
  .main-card__subject {
    max-width: 300px;
  }
}
@media (min-width: 768px) {
  .main-card__subject--small {
    max-width: 197px;
  }
}
@media (min-width: 1200px) {
  .main-card__subject--small {
    max-width: 256px;
  }
}

@media (min-width: 768px) {
  .main-card__image-container {
    grid-column: 2/-1;
  }
  .main-card__image-container--image-first {
    grid-column: 1/3;
    grid-row: 1;
  }
}

.main-card__image {
  display: block;
  width: 100%;
  height: 100%;
  background-color: #81b3d2;
  object-fit: cover;
}

.main-header {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 56px;
}

.main-header__logo {
  position: absolute;
  top: 0;
  left: 50%;
  z-index: 5;
  transform: translateX(-50%);
}
.no-js .main-header__logo {
  display: none;
}
@media (min-width: 768px) {
  .no-js .main-header__logo {
    display: block;
  }
}
.is-open .main-header__logo {
  display: none;
}
@media (min-width: 768px) {
  .is-open .main-header__logo {
    display: block;
  }
}

.main-header__logo-image {
  max-width: none;
}

.main-nav__list {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3;
  display: none;
  width: 100%;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: grid;
    position: static;
  }
}
.no-js .main-nav__list {
  position: static;
  display: block;
}
@media (min-width: 768px) {
  .no-js .main-nav__list {
    display: grid;
  }
}
.is-open .main-nav__list {
  display: block;
}
@media (min-width: 768px) {
  .is-open .main-nav__list {
    position: static;
    display: grid;
  }
}

.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 5;
  display: block;
  width: 60px;
  height: 60px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 17px;
  right: 20px;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
}
.is-open .main-nav__toggle::before, .is-open .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 26px;
  right: 17px;
  width: 27px;
  height: 4px;
  background-color: #ffffff;
  box-shadow: none;
}
.is-open .main-nav__toggle::before {
  transform: rotate(45deg);
}
.is-open .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.no-js .main-nav__toggle {
  display: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle:hover {
  opacity: 0.3;
}
.main-nav__toggle:active, .main-nav__toggle:focus {
  opacity: 0.6;
}

.map {
  padding-top: 10px;
  padding-bottom: 10px;
}

.map__image-container {
  position: relative;
  height: 350px;
}

.map__image {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  display: block;
  width: 100%;
  height: auto;
  max-height: 350px;
  background-color: #cadfaa;
  object-fit: cover;
}

.map__iframe {
  position: relative;
  z-index: 2;
  display: block;
  width: 100%;
  max-height: 350px;
  border: none;
  object-fit: cover;
}

.wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  background-color: #ffffff;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
}

.photo-item {
  position: relative;
  margin: 0;
  padding: 25px 0 25px 0;
}
.photo-item::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: -18px;
