/*Модалки*/
.modal {
  position: fixed;
  top: 0;
  left: 0;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  display: none;
}

.modal__open {
  display: flex;
}

.modal__container {
  position: relative;
  background-color: #ffffff;
  box-shadow: 0 7px 15px rgba(0, 1, 1, 0.15);
}

.modal__container--has-error .modal__content--is-valid,
.modal__container--is-valid .modal__content--has-error {
  display: none;
}

.modal__content {
  width: 100%;
  max-width: 400px;
}
.modal__content--is-valid {
  max-width: 640px;
}

.modal__content--has-error .modal__content-container {
  padding: 51px 42px 27px 41px;
}
@media (min-width: 768px) {
  .modal__content--has-error .modal__content-container {
    padding: 51px 42px 19px 41px;
  }
}

.modal__content--is-valid .modal__content-container {
  padding: 39px 39px 50px 37px;
}
@media (min-width: 768px) {
  .modal__content--is-valid .modal__content-container {
    padding: 52px 98px 52px 98px;
  }
}

.modal__title {
  margin-bottom: 25px;
}
@media (min-width: 768px) {
  .modal__title {
    font-size: 30px;
    line-height: 40px;
  }
}

.modal__button-container {
  padding: 0 70px 30px 70px;
}
@media (min-width: 768px) {
  .modal__button-container {
    padding: 29px 64px 46px 64px;
  }
}
.modal__button-container--is-valid {
  padding: 20px;
  background-color: #f2f2f2;
}
@media (min-width: 768px) {
  .modal__button-container--is-valid {
    padding: 44px 91px;
  }
}

@media (max-width: 767px) {
  .modal__close-button {
    font-size: 18px;
  }
}